* {
    box-sizing: border-box;
}

html, 
body,
#root {
    width: 100%;
    height: 100%;
}

.app-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    .main-container {
        min-width: 600px;
        height: 300px;
        display: flex;
        flex-direction: column;
        border: 1px solid #999;
        border-radius: 3px;

        .top-section {
            flex: 0 0 50%;
        }

        .bottom-section {
            flex: 0 0 50%;
        }
    }
}

.btn {
    border: 1px solid #dddddd;
    margin: 15px 0;

    &:focus {
        outline: none;
    }
}